* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}

body {
  background-color: #f5f5f5;
  text-align: center;
}
.GroceryListApp h1.heading {
  background-color: #4376FF;
  padding: 5%;
  font-size: 350%;
  margin: 0;
  color: white;
}

h1 {
  margin: 32px 0;
  font-size: 24px;
}

.complete {
  text-decoration: line-through;
  opacity: 0.4;
}

.groceryListForm {
  margin-bottom: 32px;
}

.groceryListForm-input {
  padding: 14px 32px 14px 16px;
  border-radius: 4px 0 0 4px;
  border: 2px solid #FFD800;
  outline: none;
  width: 320px;
  background: transparent;
  color: black;
}

.groceryListForm-input::placeholder {
  color: gray;
}

.groceryListForm-button {
  padding: 16px;
  border: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  outline: none;
  background: #FFD800;
  color: #fff;
  text-transform: capitalize;
}

.groceryListForm-button.edit {
  padding: 16px 22px;
}

.groceryListForm-container {
  display: flex;
  flex-direction: row;
  position: relative;
}

.grocery-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 4px auto;
  color: black;
  background: #FFD800;
  padding: 16px;
  border-radius: 5px;
  width: 90%;
}

.icons {
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
}

.delete-icon {
  margin-right: 5px;
  color: #fff;
}

.edit-icon {
  color: #fff;
}
